<template>
	<div>
		<div class="hero is-fullheight hero-mathquestionary-polka-points is-danger is-bold">
			<navbar></navbar>
			<div class="hero-body">
				<div class="container has-text-centered">
					<h2 class="title is-3 has-text-white">
						Acepta el reto !
					</h2>
					<a class="button is-white is-hidden-touch is-medium is-outlined" :class="playButton.clases" @click="play()">PLAY NOW</a>
					<a :class="playButton.clases" @click="play()" class="button is-danger is-medium is-hidden-desktop">
						PLAY NOW
					</a>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import navbar from '@/components/member-side/navbar';
	export default{
		components:{navbar},
		data:()=>({
			playButton:{
				clases: ''
			}
		}),
		methods:{
			play()
			{
				this.playButton.clases = 'is-loading';
				setTimeout(()=> this.playButton.clases ='',1000)
			}
		}
	}
</script>
